module.exports = " <div id=app class=reminder-container v-cloak> <img class=logo src=" + require("./logo.png") + "> <h1 v-text=title></h1> <br/> <input id=add-input type=text v-model=newItem @keyup.enter=addNew placeholder=enter输入 /> <ul class=reminders> <li v-for=\"item in items\"> <h3 @mouseenter=itemEnter(item) @mouseleave=itemLeave(item)> <div class=box v-bind:class=\"{ 'checked': item.checked }\" @click=chooseCheck(item)> <i class=checkbox-icon></i> </div> <p class=item-label v-bind:class=\"{ 'line-through': item.checked }\">{{ $index + 1 }}.{{ item.label }} </p> <p class=\"item-status done\" v-if=item.checked>已完成</p> <p class=item-status v-else>未完成</p> <p class=item-delete v-if=item.showDelete @click=openDialog()>&#215;</p> </h3> <div class=dialog v-if=show @click=close()> <div class=del-confirm-box> <div class=del-confirm-text> <img src=" + require("./app-sure-del.png") + "> <span>你确定要删除吗?</span> <span class=forver>此条信息将会被永久删除</span> </div> <div class=del-confirm-btn> <span class=del-confirm-sure @click=deleteClick(item)>删除</span> <span class=del-confirm-cancel @click=close()>取消</span> </div> </div> </div> </li> </ul> <div class=mask v-if=show @click=close()></div> <div class=notification v-if=delflag transition=slide> <div class=content> 删除成功 </div> </div> <div class=bt> <div class=box v-bind:class=\"{ 'checked': changecheck }\" @click=change()> <i class=checkbox-icon></i> </div> <span class=all>{{!changecheck ? \"全选\" : \"反选\" }}</span> <span @click=allDel() class=clear-all>清空</span> </div> <div class=\"events clearfix\"> <span class=fl>待办事项<strong class=hope>{{items.length - doneResult() }}</strong></span> <span class=fl>完成事项<strong class=done>{{doneResult()}}</strong></span> <span class=fr>总共事项<strong class=total>{{items.length}}</strong></span> </div> </div> ";


